$('.base-header__dropdown-link').each(function() {
  let pathname = location.pathname.split('/')
  pathname.splice(1, 1)
  pathname = pathname.join('/')
  if(pathname && $(this).attr('href') === pathname) {
    $(this).addClass('active')
  } else {
    $(this).removeClass('active')
  }
})

function signOut() {
  cookie.remove('Swj.Share')
  history.go(0)
}
