$('.base-footer__select-lang select').val(getLanguage().toLowerCase());

$('.base-footer__select-lang .dropdown-item').click(function(e) {
  var pathname = location.pathname.split('/')
  pathname[1] = $(e.target).data('value')
  $('.base-footer__select-lang select').val(pathname[1])
  location.href = location.origin + pathname.join('/')
})
